<template>
    <div class="container">
        <v-expansion-panel>
            <v-expansion-panel-content>
                <div slot="header">User List</div>
                <v-data-table
                        :headers="headers"
                        :items="users"
                        hide-actions
                        class="elevation-1"
                >
                    <template slot="items" slot-scope="props">
                        <td>{{ id }}</td>
                        <td class="text-xs-center">{{ props }}</td>
                    </template>
                </v-data-table>
            </v-expansion-panel-content>
            <v-expansion-panel-content>
                <div slot="header">Top 5 Forms</div>
                <v-card>
                    <v-card-text>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim
                        veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
                    </v-card-text>
                </v-card>
            </v-expansion-panel-content>
        </v-expansion-panel>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "Home",
        data() {
            return {
                headers: [
                    {
                        text: 'User ssoid',
                        align: 'left',
                        sortable: false,
                        value: 'ssoid'
                    },
                    {text: 'Forms', value: 'list', sortable: false, align: 'center'}
                ],
                desserts: [
                    {
                        value: 'ssoid',
                        name: 'Frozen Yogurt',
                        calories: 159,
                    },
                    {
                        value: 'list',
                        name: 'Ice cream sandwich',
                        calories: 237,
                    }
                ],
                users: [],
                forms: null
            }
        }, methods: {},
        created() {
            axios.get('/GetUsers')
                .then(data => {
                    this.users = data.data;
                }).catch(error => {
                console.log(error);
            });
            axios.get('/GetTopForms')
                .then(data => {
                    this.users = data.data;
                }).catch(error => {
                console.log(error);
            })
        }
    }
</script>

