import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home'
import Load from './components/LoadFile'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/load',
            name: 'load',
            component: Load
        }
    ],
    mode: 'history'
})
